<template>
	<view>
		<text>这是A组件</text>
		<button type="default" @click="change()">修改B组件中的值</button>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				num:10,
			};
		},
		methods:{
			change(){
				uni.$emit('updateNum',this.num)
			}
		}
	}
</script>

